// 
<template>
  <div id="bc">
    <!--  -->
    <div id="com_blue">
      <img @click="backClick()" src="../imgs/back.png" alt>
      <span></span>
    </div>
    <!--  -->
    <div id="exVip_cen">
      <p>: {{$store.state.userMsg.username}}</p>
      <input type="text" maxlength="10" placeholder="10" v-model="inp1" @input="change()">
      <input type="text" maxlength="6" placeholder="6" v-model="inp2" @input="change()">
      <button @click="box()" :style="{backgroundColor:color}"></button>
    </div>
    <!--  -->
    <div id="exVip_bot">
      <p></p>
      <p></p>
      <p>: 30</p>
      <p>: 90</p>
      <p>: 360</p>
      <p>*34(31)</p>
    </div>
    <!--  -->
      <div id="box" v-if="showtool">
        <img src="../imgs/tishi.jpg" alt>
        <p>{{import1}}</p>
        <button @click="closeTool()"></button>
      </div>
  </div>
</template>
<script>
export default {
  name: "exchange_vip",
  data() {
    return {
      inp1: "",
      inp2: "",
      color: "#ccc",
      showtool:false,
      import1:""
    };
  },
  created() {
    this.change();
  },
  methods: {
    backClick() {
      this.$router.back();
    },
    change() {
      if (this.inp1.length == 10 && this.inp2.length == 6) {
        this.color = "red";
      } else {
        this.color = "#ccc";
      }
    },
    box(){
        if(this.inp1==""||this.inp2==""){
            this.showtool=true;
            this.import1=""
        }else{
            this.showtool=true;
            this.import1=""
        }
    },
    closeTool(){
        this.showtool=false;
    }
  }
};
</script>
<style scoped>
/*  */
#exVip_cen p {
  margin: 0.01rem 0 0.1rem 0.1rem;
  font-size: 0.15rem;
}
#exVip_cen input {
  box-sizing: border-box;
  width: 100%;
  padding: 0.1rem;
  font-size: 0.15rem;
  border: 0;
  border-bottom: 0.01rem solid #ddd;
}
#exVip_cen button {
  width: 92%;
  margin-left: 4%;
  text-align: center;
  color: white;
  font-size: 0.15rem;
  margin-top: 0.1rem;
  padding: 0.1rem;
  border: 0;
  border-radius: 0.05rem;
}
/*  */
#exVip_bot {
  margin: 0.1rem 0.25rem 0 0.35rem;
  color: gray;
}
#exVip_bot p {
  font-size: 0.13rem;
  padding-top: 0.05rem;
}
#exVip_bot p:nth-of-type(1) {
  text-align: center;
  font-size: 0.16rem;
  margin-bottom: 0.1rem;
}
</style>



