// 
<template>
    <div id="bc">
         <!--  -->
        <div id="com_top">
            <img @click="backClick()" src="../imgs/back.png" alt="">
            <span></span>
            <div id="now">
                <span></span>
                <div>
                    <span></span>
                    <span @click="goEx()"></span>
                </div>
                <p>0</p>
                <button @click="openTool()" id="but"></button>
            </div>
        </div>
        <!--  -->
        <div id="com_botton">
            <p>30</p>
            <div>
                <img src="../imgs/xiaoren.png" alt="">
                <p>30</p>
                <p></p>
            </div>
        </div>
        <!--  -->
      <div id="box" v-if="showtool">
        <img src="../imgs/tishi.jpg" alt>
        <p></p>
        <button @click="closeTool()"></button>
      </div>
    </div>
</template>
<script>
export default {
    name:"myjifen",
    data() {
        return {
            showtool:false
        }
    },
    methods: {
        backClick(){
            this.$router.back();
        },
        goEx(){
            this.$router.push({
                name:"myjifen_ex"
            })
        },
        closeTool(){
            this.showtool=false
        },
        openTool(){
            this.showtool=true
        }
    },
}
</script>
<style scoped>
    #com_botton p:nth-of-type(2){
        font-size: 0.14rem;
        color: gray;
    }
</style>



