// 
<template>
  <div id="bc">
    <!--  -->
    <div id="com_top">
      <img @click="backClick()" src="../imgs/back.png" alt>
      <span></span>
      <div id="now">
        <span></span>
        <div>
          <span></span>
          <span @click="goEx()"></span>
        </div>
        <p>0.00</p>
        <button id="but"></button>
      </div>
    </div>
    <!--  -->
    <div id="com_botton">
      <p></p>
      <div>
        <img src="../imgs/xiaoren.png" alt>
        <p></p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "myyue",
  methods: {
    backClick() {
      this.$router.back();
    },
    goEx() {
      this.$router.push({
        name: "myyue_ex"
      });
    }
  }
};
</script>
<style scoped>
#but {
  background-color: #ccc;
}
</style>



